@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #1b315a;
  --color-bg-dark: #111b35;
  --color-bg-variant: #283f8b;
  --color-primary: #379af7;
  --color-primary-variant: rgb(129, 202, 255);
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);
  --color-green: rgb(69, 212, 117);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Poppins", sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(../src/assets/random-grey-variations.png);
}

/* -------------------- General Styles -------------------- */

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

hi,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-white);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  border-radius: 1rem;
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* -------------- Responsive Media Queries (Large Size) ----------------*/

@media screen and (min-width: 1600px) {
  h2 {
    font-size: 2.5rem;
  }
}

/* -------------- Responsive Media Queries ( Medium Size) ----------------*/

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

/* -------------- Responsive Media Queries ( Small Size) ----------------*/

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }
}
